body {
  margin: 0;
  padding: 0;
  border: 0;
}

#content {
  position: absolute;
  top: 0;
  width: 250px;
  height: clamp(300px, 50vh, 00px);
  border-radius: 12px;
  padding: 1em 2em;
  background-color: hsla(360, 100%, 100%, 0.82);
  backdrop-filter: blur(5px);
  overflow-y: auto;
  box-shadow: 8px 5px 6px -8px rgba(0,0,0,0.75);

  /* const articleStyle = {
    padding: "1em",
    borderRadius: ".5em",
    maxHeight: "100vh",
    overflowY: "auto",
    backgroundColor: "hsla(360, 100%, 100%, 0.82)",
    backdropFilter: "blur(5px)",
  }; */
}

.circle circle {
  transition: stroke-width 250ms ease-out, fill 250ms ease-out;
}
