body {
  border: 0;
  margin: 0;
  padding: 0;
}

#content {
  width: 250px;
  height: clamp(300px, 50vh, 0px);
  -webkit-backdrop-filter: blur(5px);
  backdrop-filter: blur(5px);
  background-color: #ffffffd1;
  border-radius: 12px;
  padding: 1em 2em;
  position: absolute;
  top: 0;
  overflow-y: auto;
  box-shadow: 8px 5px 6px -8px #000000bf;
}

.circle circle {
  transition: stroke-width .25s ease-out, fill .25s ease-out;
}

